import {extensionSpec} from 'jimu-core';

export default class ArcGISDependencyDefine implements extensionSpec.DependencyDefine{
  id = 'arcgis-dependency-define';

  getDependencyKey(){
    return 'arcgis';
  }

  getResources(){
    return [
      `${window.arcgisApiUrl}dojo/dojo.js`,
      `${window.arcgisApiUrl}esri/css/main.css`
    ]
  }
}