import {init, AppRoot, React, ReactDOM, ExtensionManager, ExtensionPoints} from 'jimu-core';
import ArcGISDependencyExtension from 'jimu-arcgis/arcgis-dependency-extension';
import './index.scss';

init().then(() => {
  ExtensionManager.getInstance().registerExtension({
    epName: ExtensionPoints.DependencyDefine,
    extension: new ArcGISDependencyExtension()
  });
  
  ReactDOM.render(
    <AppRoot/>,
    document.getElementById('app')
  )
}, err => {
  console.error(err);
});
